:root {
  --primary-text-color: #757575;
  --title-text-color: #212121;
  --accent-color: #2196f3;
  --accent-active-color: #188ce8;
  --primary-white-color: #fff;
  --primary-black-color: #000;
  --dark-bg-color: #2f303a;
  --light-bg-color: #f5f4fa;
  --hero-gradient-color: #2f303a66;
  --icon-bg-color: #afb1b8;
}

.visually-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  list-style: none;
}

body {
  background-color: var(--primary-white-color);
  color: var(--primary-text-color);
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@media screen and (width >= 1200px) {
  .section {
    padding-top: 94px;
    padding-bottom: 94px;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (width >= 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (width >= 768px) {
  .container {
    max-width: 720px;
  }
}

@media screen and (width >= 1200px) {
  .container {
    max-width: 1200px;
  }
}

.section-title {
  text-align: center;
  letter-spacing: .03em;
  color: var(--title-text-color);
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.18;
}

@media screen and (width >= 1200px) {
  .section-title {
    margin-bottom: 50px;
    font-size: 36px;
    line-height: 1.17;
  }
}

.backdrop {
  opacity: 1;
  background: #0003;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  background: var(--primary-white-color);
  border-radius: 4px;
  width: 95%;
  max-height: 95%;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

@media screen and (width >= 480px) {
  .modal {
    width: 460px;
  }
}

@media screen and (width >= 1200px) {
  .modal {
    width: 528px;
  }
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal-close {
  background: var(--primary-white-color);
  cursor: pointer;
  border: 1px solid #0000001a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-close .icon {
  fill: currentColor;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-close:hover .icon {
  fill: var(--accent-color);
}

.modal-title {
  text-align: center;
  letter-spacing: .03em;
  color: var(--title-text-color);
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.5;
}

@media screen and (width >= 1200px) {
  .modal-title {
    line-height: 1.15;
  }
}
